import React from "react";
import { sysAppExplorerStyles } from "./style.js";
import TrashBin from "../../systemApplications/TrashBin";
import AppViewer from "../../systemApplications/AppViewer";
import Clock from "../../systemApplications/Clock";
import Notes from "../../systemApplications/Notes";
import Setting from "../../systemApplications/Setting";

class SysAppExplorerGUI extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div
        style={{
          ...sysAppExplorerStyles.section,
          width: Math.floor((this.props.dims.width * 0.9) / 100) * 100,
        }}
      >
        <div style={sysAppExplorerStyles.title}>{"Utilities"}</div>
        <div style={sysAppExplorerStyles.appBar}>
          <TrashBin fileSystem={this.props.fileSystem} />
          <AppViewer
            toggleAppExplorer={this.props.appFuncs.toggleAppExplorer}
          />
          <Notes
            systemData={this.props.systemData}
            updateSystemData={this.props.updateSystemData}
          />
          <Setting
            fileSystem={this.props.fileSystem}
            systemData={this.props.systemData}
            updateSystemData={this.props.updateSystemData}
          />
          <Clock
            systemData={this.props.systemData}
            updateSystemData={this.props.updateSystemData}
          />
        </div>
      </div>
    );
  }
}

export default SysAppExplorerGUI;
