import React from "react";
import styles from "../../styles";
import AppIcon from "../../components/AppIcon";
import { DeleteOutlined } from "@ant-design/icons";
import WindowedFileExplorer from "../../components/WindowedFileExplorer";
import { themeColors } from "../../config.js";

class TrashBin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
    };
    this.toggleWindow = this.toggleWindow.bind(this);
  }

  toggleWindow() {
    this.setState((state) => ({
      ...state,
      open: !state.open,
    }));
  }

  render() {
    if (this.state.open) {
      return (
        <div>
          <WindowedFileExplorer
            fileSystem={this.props.fileSystem}
            id={"bin"}
            name={"Recycle Bin"}
            root={"bin"}
            closeWindow={this.toggleWindow}
          />
          <AppIcon
            key={"trash_bin"}
            icon={
              <DeleteOutlined
                style={{ fontSize: 30, color: themeColors.yellow }}
              />
            }
            name={"Recycle Bin"}
            width={100}
            height={110}
            onClick={this.toggleWindow}
            nameOnChange={false}
            nameChangable={false}
          />
        </div>
      );
    } else {
      return (
        <AppIcon
          key={"trash_bin"}
          icon={
            <DeleteOutlined
              style={{ fontSize: 30, color: themeColors.yellow }}
            />
          }
          name={"Recycle Bin"}
          width={100}
          height={110}
          onClick={this.toggleWindow}
          nameOnChange={false}
          nameChangable={false}
        />
      );
    }
  }
}

export default TrashBin;
