import Window from "../../components/Window";

const SettingApp = ({ onCancel, fileSystem }) => {
  const size = fileSystem.getDirectorySize("root");
  return (
    <Window
      top={0}
      left={0}
      id={"setting"}
      name={"Setting"}
      onCancel={onCancel}
    >
      {size}
    </Window>
  );
};

export default SettingApp;
